import React from 'react';
import { Navbar } from 'react-bootstrap';

const MyNavbar = props => (
  <Navbar inverse staticTop>
    <Navbar.Brand>
      <a href="//ynkr.org">ynkr.org</a>
    </Navbar.Brand>
  </Navbar>
);

export default MyNavbar;



